import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';

import { UserService } from 'src/app/services/user.service';
import { GeneralService } from '../../../../services/general.service';


@Component({
  selector: 'app-mis-compras-clientes-public',
  templateUrl: './mis-compras-clientes-public.component.html',
  styleUrls: ['./mis-compras-clientes-public.component.css']
})
export class MisComprasClientesPublicComponent implements OnInit {

  compras: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];

  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService,
    private activatedRoute: ActivatedRoute
    
  ) { }

  ngOnInit(): void {
    this.informacionUsuario();
    this.getMisComprasUsuarios();
  }




  getMisComprasUsuarios()
  {
    const params = this.activatedRoute.snapshot.params;
    this.generalService.getOrdenUsuarios(params.id).subscribe(res => {
      this.compras = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }


  EliminarOrden(orden_id)
  {
    
    if (confirm('Desea eliminar el producto?')) 
    {
      // si
      this.generalService.deleteOrdens(orden_id).subscribe(res => {
        console.log(res);
        Swal.fire(
          'Eliminado!',
          'Producto Eliminado...',
          'error'
        );
        this.getMisComprasUsuarios();
     },
     err => {
       console.log(err);
       if (err.status === 0)
       {
         Swal.fire(
           'Error!',
           'El servidor no esta activo, no podra ver los datos...',
           'error'
         );
       }
     }
    );






      console.log('Eliminando');
    } else 
    {
      // Do nothing!
      console.log('No hizo nada');
    }










  
  }









  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


}
