import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-menu-clientes',
  templateUrl: './menu-clientes.component.html',
  styleUrls: ['./menu-clientes.component.css']
})
export class MenuClientesComponent implements OnInit {

  FamiliaresMenu: boolean = false;
  ComprasMenu: boolean = false;
  usuarios: any = [];



  constructor(private auth: UserService,  private router: Router) { }

  ngOnInit(): void {
    this.informacionUsuario();
  }



  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }

  logout() {
    this.auth.logout();
  }





}
