import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';

import { UserService } from 'src/app/services/user.service';
import { GeneralService } from '../../../../services/general.service';

@Component({
  selector: 'app-familares-list-public',
  templateUrl: './familares-list-public.component.html',
  styleUrls: ['./familares-list-public.component.css']
})
export class FamilaresListPublicComponent implements OnInit {

  familiares: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];

  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService
  ) { }

  ngOnInit(): void {
    this.getFamiliares()
  }

  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  getFamiliares()
  {
    this.generalService.getFamiliares().subscribe(res => {
      this.familiares = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }



}
