import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { HttpClient, HttpEventType } from '@angular/common/http';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';


@Component({
  selector: 'app-familares-form-public',
  templateUrl: './familares-form-public.component.html',
  styleUrls: ['./familares-form-public.component.css']
})
export class FamilaresFormPublicComponent implements OnInit {

  familiares: any = [];
  usuarios: any = [];
  edit = false;

 createFormGroup() {
    return new FormGroup({
      cedula: new FormControl('', [Validators.required]),
      nombres: new FormControl('', [Validators.required]),
      apellidos: new FormControl('', [Validators.required]),
      telefono: new FormControl('', [Validators.required]),
      fecha_nacimiento: new FormControl('', [Validators.required]),
      direccion: new FormControl('', [Validators.required] ),
      user_id: new FormControl('', [Validators.required]),
      saldo: new FormControl('', [Validators.required]),
           
   
    });
  }


   Form: FormGroup;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private generalService: GeneralService
  ) { this.Form = this.createFormGroup(); }

  ngOnInit(): void {

    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    if (params.id)
          {
            this.generalService.getFamiliar(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.familiares = res;
                  this.Form.patchValue({
                    cedula: this.familiares.cedula,
                    nombres: this.familiares.nombres,
                    apellidos: this.familiares.apellidos,
                    telefono: this.familiares.telefono,
                    fecha_nacimiento: this.familiares.fecha_nacimiento,
                    saldo: this.familiares.saldo,
                    direccion: this.familiares.direccion,
                  
                               
                    
               });

                  this.edit = true;
                 
                },
                err => console.log(err)
              );
        }

        this.informacionUsuario();
      }


      informacionUsuario() {
        this.userService.profile().subscribe(
          res => {
           this.usuarios = res;
           // alert(this.usuarios.user.id);
           console.log('usuario data', res);
           this.Form.get('saldo').setValue('40000');
           this.Form.get('user_id').setValue(this.usuarios.user.id);
          
          },
         err => {
            alert('Sesion Experiada..');
            this.router.navigateByUrl('/login');
          }
        );
      }


  Guardar() {

    // para excluir los campo cuando se vayan a guardar
    this.generalService.saveFamiliares(this.Form.value).subscribe(
      res => {
            console.log(res);
            this.router.navigate(['/familiares/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }

  Actualizar() {
  
    this.generalService.updateFamiliares(this.familiares.id, this.familiares)
      .subscribe(
        res => {
          console.log(res);
          this.router.navigate(['/familiares/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }

}
