import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GeneralService } from 'src/app/services/general.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';


@Component({
  selector: 'app-productos-clientes-public-list',
  templateUrl: './productos-clientes-public-list.component.html',
  styleUrls: ['./productos-clientes-public-list.component.css']
})
export class ProductosClientesPublicListComponent implements OnInit {

  productos: any = [];
  ordenes: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];

  Form: FormGroup;


  createFormGroup() {
    return new FormGroup({
      producto_id: new FormControl('', [Validators.required]),
      estatu_id: new FormControl('', [Validators.required]),
      user_id: new FormControl('', [Validators.required] ),
        
    });
  }


  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService
  ) { this.Form = this.createFormGroup(); }

  ngOnInit(): void {
    this.getProductos();
    this.informacionUsuario();
  }



  Guardar(compra_id) {
  this.Form.get('producto_id').setValue(compra_id);
  // alert(compra_id)
    this.generalService.saveOrdens(this.Form.value).subscribe(
      res => {
            console.log(res);
            this.getProductos();
            this.filter= '';
            // this.router.navigate(['/productos/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Productos agregados correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }











  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);
       this.Form.get('user_id').setValue(this.usuarios.user.id);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  getProductos()
  {
    this.generalService.getProductos().subscribe(res => {
      this.productos = res;
      console.log(res);
     //this.Form.get('producto_id').setValue(this.productos[1].id);
      this.Form.get('estatu_id').setValue(1);
      
     // alert(this.productos[1].id)
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }
}
