import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';

import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';



@Component({
  selector: 'app-orden-compras-en-espera-listado-admin-local',
  templateUrl: './orden-compras-en-espera-listado-admin-local.component.html',
  styleUrls: ['./orden-compras-en-espera-listado-admin-local.component.css']
})
export class OrdenComprasEnEsperaListadoAdminLocalComponent implements OnInit {

  orden: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];


  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService
  ) { }


  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  ngOnInit(): void {

    this.getOrdenEnEspera();
  }



  getOrdenEnEspera()
  {
    this.generalService.getOrdenEnEspera().subscribe(res => {
      this.orden = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }

}
